<template>
    <div>
        <div class="btn">
            <el-button type="primary" @click="addClass">添加班级</el-button>
        </div>
        <div class="btn">
         <el-select v-model="grade" placeholder="请选择年级">
             <el-option v-for="item in gradeList" :key="item.id" :label="item.title" :value="item.id">
             </el-option>
         </el-select>
        </div>
        <el-dialog title="添加年级" :visible.sync="dialogClass">
            <div class="box">
                <el-form :model="form" label-width="80px">
                    <el-form-item label="选择年级">
                        <el-select v-model="form.grade_id" placeholder="请选择年级">
                            <el-option v-for="item in formGradeList" :key="item.id" :label="item.title" :value="item.id">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="班级名称">
                        <el-input v-model="form.title"></el-input>
                    </el-form-item>
                    <div class="submit">
                        <el-form-item>
                            <el-button type="primary" @click="newClass">提交</el-button>
                        </el-form-item>
                    </div>
                </el-form>
            </div>
        </el-dialog>

        <el-table :data="tableDate" ref="multipleTable">
            <el-table-column label="名称" type="selection" align="center"></el-table-column>
            <el-table-column prop="id" label="班级ID" align="center"></el-table-column>
            <el-table-column prop="grade" label="年级" align="center"></el-table-column>
            <el-table-column prop="title" label="班级" align="center"></el-table-column>
            <el-table-column prop="updated_at" label="更新时间" align="center"></el-table-column>
            <el-table-column label="操作" align="center">
                <template slot-scope="scope">
                    <el-button size="mini" type="success" @click="handleStudent(scope.$index,scope.row)">查看学生</el-button>
                    <el-button size="mini" type="success" @click="handleTeacher(scope.$index,scope.row)">教师列表</el-button>
                    <el-button size="mini" type="danger" @click="handleDel(scope.$index,scope.row)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        
        <!-- 查看学生 -->
        <el-dialog title="查看学生" :visible.sync="dialogStudent">
            <div class="box">
                <el-table :data="studentDate">
                    <el-table-column prop="id" label="班级总人数" align="center"></el-table-column>
                    <el-table-column prop="grade" label="学号" align="center"></el-table-column>
                    <el-table-column prop="title" label="姓名" align="center"></el-table-column>
                    <el-table-column prop="updated_at" label="性别" align="center"></el-table-column>
                    <el-table-column prop="updated_at" label="人脸头像" align="center"></el-table-column>
                </el-table>
            </div>
        </el-dialog>
        <!-- 查看教师 -->
        <el-dialog title="查看教师" :visible.sync="dialogTeacher">
            <div class="box">
                <el-table :data="TeacherDate" ref="multipleTable">
                    <el-table-column label="名称" type="selection" align="center"></el-table-column>
                    <el-table-column prop="id" label="用户ID" align="center"></el-table-column>
                    <el-table-column prop="grade" label="工号" align="center"></el-table-column>
                    <el-table-column prop="grade" label="姓名" align="center"></el-table-column>
                    <el-table-column prop="grade" label="性别" align="center"></el-table-column>
                    <el-table-column prop="grade" label="手机号" align="center"></el-table-column>
                    <el-table-column prop="title" label="学科" align="center"></el-table-column>
                    <el-table-column prop="title" label="是否为班主任" align="center"></el-table-column>
                    <el-table-column label="操作" align="center">
                        <template slot-scope="scope">
                            <el-button size="mini" type="danger" @click="handleDel(scope.$index,scope.row)">删除该老师身份</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
        </el-dialog>

        <div class="block">
            <el-pagination @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="10"
                layout="prev, pager, next, jumper" :total="totalPage">
            </el-pagination>
        </div>
    </div>
</template>

<script>
	import API from '@/api/student.js'
	
    export default {
        name: 'classms',
        data() {
            return {
                dialogClass: false,
                form: {
                    grade_id: '',
                    title: ''
                },
				formGradeList: [],
                grade: '',
                gradeList: [{
					value: 0,
					title: '全部'
				}],
                
                // 操作
                dialogStudent: false,
                studentDate: [],
                dialogTeacher: false,
                TeacherDate: [],

                tableDate: [{}],

                currentPage: 0,
                totalPage: 1
            }
        },
		mounted() {
			this.getGrade();
			this.getClass();
		},
        methods: {
			// 获取年级列表
			getGrade() {
				var self = this;
				API.grades(self.currentPage).then(res => {
					res.data.forEach(item => {
						self.gradeList.push(item)
					})
				})
			},
			addClass() {
				var self = this;
				self.dialogClass = true;
				API.grades(self.currentPage).then(res => {
					self.formGradeList = res.data;
				})
			},
			// 获取班级列表
			getClass() {
				var self = this;
				API.classes(self.currentPage).then(res => {
					self.tableDate = res.data;
					self.totalPage = res.total;
				})
			},
			newClass() {
				var self = this;
				self.gradeList.forEach(item => {
					if (self.grade === item.id) {
						self.form.grade_id = item.id
					}
					console.log(self.form.grade_id)
				})
				API._class(self.form).then(response => {
					self.$message.success('提交成功')
					self.dialogClass = false;
					self.getClass();
					self.grade = '';
					self.form = {};
					self.currentPage = 1;
				})
			},
            // 操作
            handleStudent() {
                var self = this
                self.dialogStudent = true;
            },
            handleTeacher() {
                var self = this
                self.dialogTeacher = true;
            },
            handleDel() {},
            
            
            // 分页
            handleCurrentChange(val) {
            	var self = this;
            	self.getClass();
            },
        }
    }
</script>

